.index-page
  .education
    width: 1200px
    margin: 0 auto
    color: black
    .title
      margin-bottom: 70px
    .row
      width: 100%
      .col
        width: 50%
        display: inline-block
        vertical-align: top
        position: relative
    .education-group
      width: 100%
      font-size: 18px
      margin-bottom: 70px
      &:last-child
        margin-bottom: 0
      .duration
        margin-bottom: 30px
      .name
        font-weight: bold
        margin-bottom: 20px
        font-size: 26px
        opacity: 0
      .school
        margin-bottom: 10px
        opacity: 0.7
        font-size: 16px
        opacity: 0
      .overview
        margin-top: 30px
        line-height: 2em
        padding-right: 50px
        opacity: 0
    .education-img
      width: 100%
      box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05)
      display: block
      margin-bottom: 100px
      opacity: 0
    .year
      position: absolute
      bottom: 100px
      right: 20px
      font-size: 70px
      font-weight: bold
      color: white
      text-shadow: 0px 5px 10px rgba(0, 0, 0, 0.25)
      opacity: 0
