.projects
  padding: calc((100% - 1200px) / 2)
  padding-top: 100px
  position: relative
  .title
    margin-bottom: 40px
  .overall
    font-size: 18px
    width: 50%
    line-height: 2em
  .row
    width: 100%
    margin-top: 80px
  .project-detail
    display: inline-block
    vertical-align: top
    width: 65%
    position: relative
    opacity: 0
    img
      width: 100%
      height: 500px
      object-fit: cover
      box-shadow: 0 10px 20px rgba(0, 0, 0, 0.25)
      display: block
    .feature-next-img
      position: absolute
      top: 0
      left: 0
    .mask
      content: ''
      width: 100%
      height: 50%
      left: 0
      bottom: 0
      position: absolute
      background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 1))
    .project-info
      position: absolute
      bottom: 40px
      left: 50%
      transform: translateX(-50%)
      text-align: center
      .project-title
        font-size: 48px
        font-weight: bold
        text-shadow: 0px 5px 10px rgba(0, 0, 0, 0.25)
        color: white
        margin-bottom: 0
        overflow: hidden
        height: 1em
        .project-title-inner
          display: inline-block
      .project-overview
        color: white
        font-size: 18px
        text-shadow: 0px 5px 10px rgba(0, 0, 0, 0.25)
        .year
          font-weight: bold
  .project-selectors
    width: 35%
    position: relative
    display: inline-block
    vertical-align: top
    .project-selector
      width: 100%
      margin-bottom: 35px
      color: rgba(0, 0, 0, 0.4)
      cursor: pointer
      transition: all 0.5s
      opacity: 0
      display: block
      text-decoration: none
      .project-number
        font-size: 36px
        display: inline-block
        vertical-align: middle
        margin-left: -53px
        margin-right: 10px
        transform: rotate(-90deg)
        font-weight: bold
        width: 42px
      .separator
        width: 5px
        height: 36px
        background: rgba(0, 0, 0, 0.4)
        display: inline-block
        vertical-align: middle
        margin-right: 20px
      .project-name
        font-size: 24px
        vertical-align: middle
        font-weight: bold
      &.active
        color: black
        .separator
          background: black
  .see-more
    font-size: 20px
    display: inline-block
    color: black
    text-decoration: none
    position: relative
    cursor: pointer
    margin-left: 30px
    opacity: 0.5
    transition: opacity 0.3s
    vertical-align: middle
    .icon-container
      width: 0
      opacity: 0
      overflow: hidden
      display: inline-block
      vertical-align: middle
      transition: opacity 0.8s, width 0.5s
      margin-left: 10px
    &:hover
      opacity: 1
    &:hover .icon-container
      width: 30px
      opacity: 1
  .vector
    position: absolute
    left: 0
    top: 250px
    z-index: -1
    width: 100%
