.menu
  width: 100%
  height: 0
  overflow: hidden
  position: fixed
  top: 0
  left: 0
  background: #4834d4
  z-index: 10
  color: white
  .menu-content
    width: 1000px
    display: flex
    flex-direction: column
    align-items: center
    margin: 0 auto
    margin-top: 100px
  .main-menu
    font-size: 80px
    list-style: none
    padding: 0
    margin: 0
    font-weight: bold
    .menu-item
      margin-bottom: 80px
      cursor: pointer
      position: relative
      text-align: center
      a
        color: white
        text-decoration: none
      &::after
        width: 0%
        height: 10px
        position: absolute
        top: 110%
        left: 50%
        transform: translateX(-50%)
        content: ''
        background: white
        transition: all 0.5s
      &:hover::after
        width: 100px
    .menu-divider
      height: 30px
  .other
    width: 100%
    text-align: center
    .social
      margin-top: 20px
      .social-media
        text-align: center
        display: inline-block
        padding: 8px 12px
        margin-right: 20px
        i
          font-size: 45px
          color: white
        &:hover
          background: rgba(0, 0, 0, 0.1)
          border-radius: 10px
          cursor: pointer
        &:last-child
          margin-right: 0px
