.projects-page
  .project-list
    z-index: 1
    position: relative
    width: 1200px
    margin: 150px auto 50px auto
    .left-column, .right-column
      width: 50%
      line-height: 2em
      font-size: 18px
      display: inline-block
      vertical-align: top
      box-sizing: border-box
    .left-column
      padding-right: 50px
      .short-word
        display: block
        margin-bottom: 30px
        opacity: 0
    .right-column
      padding-left: 50px
    .project
      width: 100%
      padding-top: 30px
      box-sizing: border-box
      transition: all 0.3s
      cursor: pointer
      opacity: 0
      margin-bottom: 100px
      display: block
      text-decoration: none
      color: inherit
      .description
        padding: 0 30px
        padding-bottom: 40px
      .title
        padding: 0 30px
        position: relative
        margin-bottom: 60px
        &::after
          position: absolute
          left: 30px
          top: 160%
          width: 40px
          height: 5px
          background: white
          content: ''
          transition: all 0.3s
      .image
        transition: all 0.3s
        width: 100%
        img
          width: 100%
          display: block
      &:hover
        opacity: 1!important
        .image
          transform: scale(1.1)
          box-shadow: 0 15px 20px rgba(0, 0, 0, 0.25)
        ::after
          width: 60px
          top: 150%
      &.blue
        background: rgba(1, 101, 255, 1)
        color: white
      &.green
        background: #00C586
        color: white
      &.red
        background: #db2828
        color: white
      &.yellow
        background: #fdcb6e
        color: white
        &:hover
          color: black
        &:hover ::after
          background: black
      &.purple
        background: #5f27cd
        color: white
      &.black
        background: #2d3436
        color: white
