.index-page
  .about
    padding-top: 100px
    width: 1200px
    margin: 0 auto
    .row
      margin-bottom: 70px
      &:first-child
        margin-bottom: 30px
      &:last-child
        margin-bottom: 0
    .left-side, .right-side
      display: inline-block
      vertical-align: top
    .left-side
      width: calc(100% / 3)
      .number-fact
        width: 100%
        .number
          font-size: 72px
          font-weight: bold
          font-family: 'Work Sans'
          margin-bottom: 10px
        .description
          font-size: 24px
    .right-side
      width: calc(100% / 3 * 2)
      #overall
        font-size: 24px
        line-height: 2em
      .text-fact
        width: 50%
        display: inline-block
        vertical-align: top
        padding-right: 50px
        box-sizing: border-box
        .title
          font-size: 24px
          font-weight: bold
          margin-bottom: 24px
        .description
          font-size: 18px
          line-height: 2em
    .reveal-0
      display: inline-block
      overflow: hidden
      height: 1.6em
      .letter
        display: inline-block
    .reveal-1
      display: inline-block
      overflow: hidden
      span
        display: inline-block
