.index-page
  .intro
    width: 1200px
    margin: 0 auto
    #intro-svg
      position: absolute
      top: 0
      left: 0
      z-index: -1
    #scroll-down-button
      width: 25px
      height: 50px
      border: 3px solid rgba(0, 0, 0, 0.5)
      border-radius: 20px
      position: absolute
      bottom: 20px
      left: 50%
      transform: translateX(-50%)
      &:before
        content: ''
        width: 10px
        height: 10px
        background: rgba(0, 0, 0, 0.5)
        position: absolute
        left: 50%
        transform: translateX(-50%)
        top: 5px
        animation: scrollbutton 3s infinite
        border-radius: 50%
    #intro-content
      position: relative
      top: 120px
    #left-part, #right-part
      display: inline-block
    #left-part
      position: relative
      right: 10px
      top: 20px
      #image-wrapper
        width: 580px
        overflow: hidden
        position: relative
        box-shadow: 0 5px 15px rgba(0, 0, 0, 0.25)
      #intro-me
        width: 600px
        display: block
    #right-part
      position: relative
      top: 87px
      left: 60px
      #name
        font-size: 60px
        font-weight: bold
        position: relative
        margin-bottom: 60px
      .text-wrapper
        position: relative
        display: inline-block
        padding-right: 0.05em
        overflow: hidden
      .letter
        transform-origin: 0 100%
        display: inline-block
        line-height: 1em
      #name-underline
        width: 0%
        height: 10px
        background: #0165FF
        position: absolute
        top: 110%
        left: 5px
      #overall
        font-size: 24px
        line-height: 1.5em

@keyframes scrollbutton
  0%
    top: 5px
    opacity: 0
  25%
    top: 5px
    opacity: 1
  75%
    top: 30px
    opacity: 1
  to
    top: 30px
    opacity: 0
