.contact
  padding: 0 calc((100% - 1200px) / 2)
  padding-top: 50px
  position: relative
  padding-bottom: 100px
  background: #0165FF
  color: white
  & > .title
    margin-bottom: 40px
    text-align: center
    color: white
    &::after
      left: 50%!important
      transform: translateX(-50%)

  .left
    width: calc(100% / 3)
    display: inline-block
    vertical-align: top
  .right
    width: calc(100% / 3 * 2)
    display: inline-block
    vertical-align: top
    .short-words
      font-size: 24px
      font-family: 'Work Sans'
      margin-bottom: 100px
      .actions
        height: 1em
        overflow: hidden
        vertical-align: top
        display: inline-block
        ul
          list-style: none
          margin: 0
          padding: 0
          .action
            font-weight: bold
    .contact-method
      display: inline-block
      vertical-align: middle
      a
        color: inherit
        text-decoration: none
    .primary
      margin-right: 100px
      font-size: 24px
      .content
        position: relative
        &::after
          width: 100%
          height: 1px
          background: white
          content: ''
          position: absolute
          left: 0
          top: 130%
    .social
      .social-media
        text-align: center
        display: inline-block
        padding: 8px 12px
        margin-right: 20px
        i
          font-size: 45px
        &:hover
          background: rgba(0, 0, 0, 0.1)
          border-radius: 10px
          cursor: pointer
    .secondary .title
      opacity: 0.6
