.mobile-block
  width: 100%
  height: 100%
  position: fixed
  top: 0
  left: 0
  padding: 25px
  box-sizing: border-box
  line-height: 2em
  background: #0165FF
  color: white
  .view-work
    font-style: italic
  .social
    margin-top: 30px
    display: flex
    justify-content: space-between
    .social-media
      text-align: center
      display: inline-block
      i
        font-size: 45px
        color: white
      &:hover
        background: rgba(0, 0, 0, 0.1)
        border-radius: 10px
        cursor: pointer
