.projects-page
  .intro
    width: 1200px
    margin: 0 auto
    padding-top: 120px
    .left, .right
      width: 50%
      position: relative
      display: inline-block
      vertical-align: top
    .left
      .title
        font-size: 72px
        position: relative
        &::after
          content: ''
          width: 100px
          height: 10px
          background: #0165FF
          position: absolute
          left: 0
          top: 130%
      .description
        font-size: 18px
        margin-top: 80px
        line-height: 2em
        width: 80%
    .right
      .intro-vector
        width: 100%
      .floating-box
        position: absolute
        text-align: center
        .box
          width: 100%
        .shadow
          margin-top: 20px
      #floating-box-1
        left: 30%
        top: 20%
        width: 100px
      #floating-box-2
        right: 25%
        top: 35%
        width: 150px
      
