.menu-button
  position: fixed
  top: 35px
  right: 30px
  width: 25px
  height: 25px
  display: flex
  justify-content: space-between
  cursor: pointer
  z-index: 11
  &.open
    .dot
      background: white!important
    .dynamic
      opacity: 0
  .col
    height: 100%
    display: flex
    justify-content: space-between
    flex-direction: column
    .dot
      width: 3px
      height: 3px
      background: black
      display: inline-block
      transition: all 1s
