.project-detail
  img
    width: 100%
  .header
    width: 100%
    height: 600px
    .content
      width: 1200px
      margin: 0 auto
      padding-top: 100px
      img
        width: 50%
        box-shadow: 0 5px 15px rgba(0, 0, 0, 0.25)
        display: inline-block
        vertical-align: middle
        opacity: 0
      .info
        display: inline-block
        width: 50%
        vertical-align: middle
        padding-left: 50px
        box-sizing: border-box
        .title
          font-size: 72px
          opacity: 0

  .float-info-box
    width: 600px
    box-sizing: border-box
    padding: 20px 10px
    display: flex
    justify-content: center
    background: white
    margin: 0 auto
    margin-top: -50px
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.25)
    .part
      padding: 0 50px
      font-size: 18px
      text-align: center
      .label
        display: block
        font-weight: bold
        margin-bottom: 20px
      .value
        line-height: 2em
  .content
    width: 860px
    margin: 0 auto
    font-size: 18px
    padding-top: 80px
    line-height: 2em
    .overview
      font-size: 24px
      margin-bottom: 100px
      line-height: 2.5em
      opacity: 0
    h2
      line-height: 2em
    .tech-stack
      display: inline-flex
      flex-direction: column
      align-items: center
      margin-right: 80px
      margin-bottom: 80px
      text-decoration: none
      color: inherit
      width: 80px
      opacity: 0
      img
        margin-bottom: 10px
        width: 80px
        display: block
    .section
      margin-bottom: 200px
    .left-part, .right-part
      display: inline-block
      vertical-align: middle
    .left-part
      width: 40%
    .right-part
      width: 60%
